    <Checkbox @on-change="queryByTime()">按照时间进⾏查询及统计</Checkbox>
    <Checkbox @on-change="queryByName()">按照名称进⾏查询及统计</Checkbox>
    <!-- <Checkbox  @on-change="queryByName()">按照导演进行查询及统计</Checkbox>
    <Checkbox @on-change="queryByName()">按照演员进行查询及统计</Checkbox>-->
