        <Row>
          <Col span="11">
            <DatePicker type="date" placeholder="Select date" v-model="formItem.time_from"></DatePicker>
          </Col>
          <Col span="2" style="text-align: center">-</Col>
          <Col span="11">
            <DatePicker type="date" placeholder="Select date" v-model="formItem.time_to"></DatePicker>
          </Col>
        </Row>
      </FormItem>
      <FormItem v-show="showCollaboration" label="合作关系">
