              <Option value="a-d">合作的演员和导演</Option>
              <Option value="d-d">合作的导演</Option>
            </Select>
          </Col>
          <Col span="2" style="text-align: center">-</Col>
          <Col span="11">
            合作次数：
            <Input v-model="formItem.collaborationTime" placeholder="输入合作次数..."></Input>
          </Col>
        </Row>
      </FormItem>
      <FormItem v-show="showType" label="类型">
        <Select v-model="formItem.type">
          <Option value="action">Action</Option>
          <Option value="fiction">Fiction</Option>
        </Select>
      </FormItem>
      <FormItem v-show="showType" label="版本">
        <Select v-model="formItem.version">
          <Option value="action">DVD</Option>
        </Select>
      </FormItem>
      <FormItem>
        <Button type="primary" @click="submit">Submit</Button>
        <Button style="margin-left: 8px">Cancel</Button>
      </FormItem>
    </Form>
    <div v-show="showResult" style="background:#eee;padding: 20px">
        <Card :bordered="false">
