            <p>{{result.time}}||{{result.data.length}}</p>
        </Card>
    </div>
     <div class="overflow-auto" v-show="showResult">
    <b-table 
      id="my-table"
      :items="items"
      :per-page="perPage"
      :current-page="currentPage"
      small
    ></b-table>
        <b-pagination
      v-model="currentPage"
      :total-rows="rows"
      :per-page="perPage"
      aria-controls="my-table"
    ></b-pagination>
    <p class="mt-3">Current Page: {{ currentPage }}</p>
  </div>
  </div>
</template>
<script>
export default {
  data() {
    return {
      formItem: {
        idList: [],
        actorName: "",
        directorName: "",
        mainActorName: "",
        commenterName: "",
        movieName: "",
        scoreSlider: [0, 0],
        type: "",
        version:"",
        time_from: new Date(),
        time_to: new Date(),
        collaborationType:"",
        collaborationTime:0,
      },
      database:"",
      showCollaboration:false,
      showTime: false,
      showName: false,
      showType: false,
      showCommenter: false,
      showScore: false,
      showResult: true,
      result: {
        time: 0,
        data: []
      },
      info:"",
      perPage: 20,
      currentPage: 1,
      items: [
        { id: 1, first_name: "Fred", last_name: "Flintstone" },
        { id: 2, first_name: "Wilma", last_name: "Flintstone" },
        { id: 3, first_name: "Barney", last_name: "Rubble" },
        { id: 4, first_name: "Betty", last_name: "Rubble" },
        { id: 5, first_name: "Pebbles", last_name: "Flintstone" },
        { id: 6, first_name: "Bamm Bamm", last_name: "Rubble" },
        { id: 7, first_name: "The Great", last_name: "Gazzoo" },
        { id: 8, first_name: "Rockhead", last_name: "Slate" },
        { id: 9, first_name: "Pearl", last_name: "Slaghoople" }
      ]
    };
  },
  computed: {
    rows() {
      return this.items.length;
    }
  },
  methods: {
    tip(val) {
      return val / 10;
    },
    queryByTime() {
      this.showTime = !this.showTime;
      console.log(this.showTime);
    },
    queryByName() {
      this.showName = !this.showName;
      console.log(this.showName);
    },
    queryByType() {
      this.showType = !this.showType;
      console.log(this.showType);
    },
    queryByCommenter() {
      this.showCommenter = !this.showCommenter;
      console.log(this.showComment);
    },
    queryByScore() {
      this.showScore = !this.showScore;
      console.log(this.showScore);
    },
    queryByCollaboration() {
      this.showCollaboration = !this.showCollaboration
      console.log(this.showCollaboration);
    },
    submit() {
      var idList=[]
      if(this.showTime){
        idList.push("1")
      }else if(this.showName){
        idList.push("2")
      }else if(this.showType){
        idList.push("3")
      }else{
        idList.push("4")
      }
      var actors=[this.formItem.actorName]
      var comment={}
      comment.id=(this.showCommenter ? 1 : 2);
      comment.username=this.commenterName
      comment.score_from=this.formItem.score_from/10
      comment.score_to=this.formItem.score_to/10
      var directors=[this.formItem.directorName]
      var main_actor=this.formItem.mainActorName
      var movie_name=this.formItem.movieName
      console.log(this.formItem.time_from)
      var time_from={
        day_of_month:this.formItem.time_from.getDate(),
        day_of_week:Math.ceil(this.formItem.time_from.getDate()/7),
        month:this.formItem.time_from.getMonth,
        quarter:Math.ceil(this.formItem.time_from.getDate()/3),
        year:this.formItem.time_from.getFullYear(),
      }
      var time_to={
        day_of_month:this.formItem.time_to.getDate(),
        day_of_week:Math.ceil(this.formItem.time_to.getDate()/7),
        month:this.formItem.time_to.getMonth,
        quarter:Math.ceil(this.formItem.time_to.getDate()/3),
        year:this.formItem.time_to.getFullYear(),
      }
      var type=this.formItem.type
      var version=this.formItem.version
      var answerType="data"
      var answer="title"//??????????
      var time=new Date()
      var url
      if(this.database=="mysql"){
        url="/MySQL/specify"
      }else if(this.database=="hbase"){
        url="/HBase/specify"
      }else if(this.database=="neo4j"){
        url="/Neo4j/specify"
      }
      var params={
        idList:idList,
        actors:actors,
        comment:comment,
        directors:directors,
        main_actor:main_actor,
        movie_name:movie_name,
        time_from:time_from,
        time_to:time_to,
        type:type,
        version:version,
        answerType:answerType,
        answer:answer,
        time:time,
      }
      console.log(params) 
      this.$axios
        .post(url,params)
        .then(response =>{
          var items=[]
          for(var data in response.data){
            items.push({result:data})
          }
          this.items=items
          this.showResult=true
          this.time=response.time
        })
        .catch(function(error) {
          console.log(error);
        });
    }
  }
};
</script>
